// This file provides the default MSAL configuration for the add-in project.

import { LogLevel } from "@azure/msal-browser";
import { createLocalUrl } from "./createLocalUrl";

/* global console */

export const clientId = "043652b6-bdb4-479c-a426-d2050f781fd2";

// Function to get the appropriate redirect URI based on environment
const getRedirectUri = () => {
  const origin = window.location.origin;
  
  // Check if we're in development (localhost)
  if (origin.includes('localhost') || origin.includes('127.0.0.1')) {
    return createLocalUrl("auth.html");
  }
  
  // For production (GitHub Pages), use the specific redirect URI
  if (origin.includes('codewithabubakr.github.io')) {
    return "brk-multihub://codewithabubakr.github.io/This-is-a-Phish/auth.html";
  }
  
  // Fallback to local URL construction
  return createLocalUrl("auth.html");
};

// Function to get the appropriate post-logout redirect URI
const getPostLogoutRedirectUri = () => {
  const origin = window.location.origin;
  
  // Check if we're in development (localhost)
  if (origin.includes('localhost') || origin.includes('127.0.0.1')) {
    return createLocalUrl("auth.html");
  }
  
  // For production (GitHub Pages), use the specific redirect URI
  if (origin.includes('codewithabubakr.github.io')) {
    return "brk-multihub://codewithabubakr.github.io/This-is-a-Phish/auth.html";
  }
  
  // Fallback to local URL construction
  return createLocalUrl("auth.html");
};

export const msalConfig = {
  auth: {
    clientId,
    redirectUri: getRedirectUri(),
    postLogoutRedirectUri: getPostLogoutRedirectUri(),
  },
  cache: {
    cacheLocation: "localStorage",
  },
  system: {
    loggerOptions: {
      logLevel: LogLevel.Verbose,
      loggerCallback: (level: LogLevel, message: string) => {
        switch (level) {
          case LogLevel.Error:
            console.error(message);
            return;
          case LogLevel.Info:
            console.info(message);
            return;
          case LogLevel.Verbose:
            console.debug(message);
            return;
          case LogLevel.Warning:
            console.warn(message);
            return;
        }
      },
      piiLoggingEnabled: true,
    },
  },
};

// Default scopes to use in the fallback dialog.
export const defaultScopes = ["user.read", "Mail.ReadWrite"];
