import { AccountManager } from "../auth/authConfig";
import { makeGraphRequest } from "../auth/msgraph-helper";

const accountManager = new AccountManager();
export const deleteEmail = async (messageId: string) => {
    try {
        await accountManager.initialize();
        const accessToken = await accountManager.ssoGetAccessToken(["Mail.ReadWrite"]);

        await makeGraphRequest(
            accessToken,
            `/me/messages/${messageId}`,
            "",
            "DELETE"
        );

        console.log(`Email with ID ${messageId} deleted successfully.`);
    } catch (error) {
        console.error("Failed to delete email:", error);
    }
};