// This file provides the provides functionality to get Microsoft Graph data.

/* global console fetch */

// Add this import for TypeScript types
// @ts-ignore: RequestInit is part of the DOM lib, but add for clarity in some TS configs
// import { RequestInit } from 'node-fetch'; // Not needed in browser/Office.js, but for linter

/**
 *  Calls a Microsoft Graph API and returns the response.
 *
 * @param accessToken The access token to use for the request.
 * @param path Path component of the URI, e.g., "/me". Should start with "/".
 * @param queryParams Query parameters, e.g., "?$select=name,id". Should start with "?".
 * @param method HTTP method (GET, POST, etc.). Defaults to 'GET'.
 * @param body Optional request body for POST/PATCH requests.
 * @returns
 */
export const makeGraphRequest = async (
  accessToken: string,
  path: string,
  queryParams: string,
  method: string = "GET",
  body?: any
): Promise<any> => {
  if (!path) throw new Error("path is required.");
  if (!path.startsWith("/")) throw new Error("path must start with '/'.");
  if (queryParams && !queryParams.startsWith("?")) throw new Error("queryParams must start with '?'.");

  const fetchOptions: any = {
    method,
    headers: {
      Authorization: accessToken,
      ...(method !== "GET" && { "Content-Type": "application/json" }),
    },
    ...(method !== "GET" && body ? { body: JSON.stringify(body) } : {}),
  };

  const response = await fetch(`https://graph.microsoft.com/v1.0${path}${queryParams}`, fetchOptions);

  if (response.ok) {
    // /me/sendMail returns 202 with no content, so only parse JSON if there is content
    const contentType = response.headers.get("content-type");
    if (contentType && contentType.indexOf("application/json") !== -1) {
      const data = await response.json();
      console.log(data);
      return data;
    } else {
      return true; // Success, but no content
    }
  } else {
    throw new Error(response.statusText);
  }
}
